*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  position: relative;
  margin: 0;
}

div {
  box-sizing: border-box;
}

.header {
  margin-bottom: 100px;
  background: #f7f7f7;
  font-family: "Lato", sans-serif;
}
@media (max-width: 1279px) {
  .header {
    margin-bottom: 16px;
  }
}
@media (max-width: 767px) {
  .header {
    margin-bottom: 50px;
  }
}
.header__container {
  position: relative;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1720px;
}
.header__container::before {
  content: "";
  display: block;
  position: absolute;
  top: 5px;
  left: 28px;
  width: 456px;
  height: 56px;
  z-index: 0;
  border-radius: 11px;
  background-image: linear-gradient(270deg, #4964E6 0%, #FC5B60 100%);
  filter: blur(10px);
}
@media (max-width: 1400px) {
  .header__container::before {
    top: 0;
    width: 192px;
    left: 23px;
  }
}
@media (max-width: 1279px) {
  .header__container {
    max-width: 870px;
  }
}
@media (max-width: 767px) {
  .header__container {
    max-width: 415px;
    padding: 20px;
  }
  .header__container::before {
    z-index: -1;
    width: 135px;
    left: 26px;
    top: calc(100% - 20px);
    filter: blur(7px);
    border-radius: 8px;
  }
}
@media (min-width: 768px) and (max-width: 1199px) {
  .header__container {
    padding: 25px 0;
  }
}
@media (min-width: 1200px) {
  .header__container {
    padding: 40px 0;
  }
}
.header__link {
  position: absolute;
  top: 0;
  left: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 17px 12px;
  width: 472px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #fafafa;
  text-decoration: none;
  text-align: center;
  background-image: linear-gradient(270deg, #4964E6 0%, #FC5B60 100%);
  border-bottom-right-radius: 11px;
  border-bottom-left-radius: 11px;
}
@media (max-width: 1400px) {
  .header__link {
    width: 198px;
    font-size: 14px;
    line-height: 17px;
    padding: 10px 12px;
  }
}
@media (max-width: 767px) {
  .header__link {
    width: 145px;
    padding: 0px 0 8px;
    font-size: 11px;
    line-height: 13px;
    left: 20px;
    top: 100%;
  }
}

.logos .logos__other {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1279px) {
  .logos {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .logos .logos__other {
    width: 135px;
  }
}
@media (max-width: 767px) {
  .logos {
    width: 335px;
    display: flex;
    align-items: center;
  }
  .logos .logos__other {
    width: auto;
  }
}
@media (min-width: 768px) and (max-width: 1199px) {
  .logos {
    width: 260px;
  }
}
@media (min-width: 1200px) and (max-width: 1599px) {
  .logos {
    width: 300px;
  }
}
@media (min-width: 1600px) {
  .logos {
    width: 350px;
  }
}

.main-logo {
  width: 100%;
  padding-bottom: 33px;
  margin-bottom: 14px;
  border-bottom: 1px solid black;
}
@media (max-width: 1279px) {
  .main-logo {
    margin-bottom: 4px;
    padding-bottom: 15px;
    width: 135px;
  }
}
@media (max-width: 767px) {
  .main-logo {
    width: 118px;
    display: flex;
    padding-right: 20px;
    padding-bottom: 0;
    margin-right: 13px;
    margin-bottom: 0;
    border-bottom: 0;
    border-right: 1px solid black;
  }
}

.second-logo {
  height: 30px;
}
@media (max-width: 1279px) {
  .second-logo {
    height: 15px;
  }
}
@media (max-width: 767px) {
  .second-logo {
    height: 19px;
    margin-left: 19px;
  }
}

.main__text-container {
  margin: 0 auto;
  max-width: 1440px;
  flex-wrap: wrap;
  padding: 0 30px;
  font-family: "Lato", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 767px) {
  .main__text-container {
    padding: 0 20px;
  }
}
.main__houses-container {
  margin: 0 auto;
  max-width: 1709px;
  display: flex;
  justify-content: center;
}

.title {
  margin: 0;
  margin-bottom: 4px;
  font-weight: 500;
  text-align: center;
  color: #fc5b60;
  font-size: 60px;
  line-height: 1.1;
}
@media (max-width: 1279px) {
  .title {
    font-size: 40px;
    line-height: 48px;
  }
}
@media (max-width: 767px) {
  .title {
    max-width: 225px;
    margin-bottom: 8px;
    font-size: 32px;
    line-height: 30px;
  }
}

.subtitle {
  margin: 0;
  margin-bottom: 33px;
  font-weight: 500;
  font-size: 40px;
  line-height: 48px;
  text-align: center;
  color: #fc5b60;
}
@media (max-width: 1279px) {
  .subtitle {
    margin-bottom: 26px;
    font-size: 24px;
    line-height: 29px;
  }
}
@media (max-width: 767px) {
  .subtitle {
    max-width: 225px;
    margin-bottom: 13px;
    font-size: 13px;
    line-height: 16px;
  }
}

.disclaimer {
  position: relative;
  max-width: 950px;
  margin: 0;
  margin-bottom: 62px;
  padding-top: 18px;
  font-weight: 500;
  text-align: center;
  color: #4964E6;
  font-size: 40px;
  line-height: 48px;
}
.disclaimer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  height: 1px;
  background-color: #4964E6;
  transform: translateX(-50%);
  width: 365px;
}
@media (max-width: 1279px) {
  .disclaimer {
    max-width: 580px;
    margin-bottom: 25px;
    font-size: 24px;
    line-height: 29px;
  }
}
@media (max-width: 767px) {
  .disclaimer {
    max-width: 225px;
    margin-bottom: 32px;
    padding-top: 12px;
    font-size: 13px;
    line-height: 16px;
  }
  .disclaimer::before {
    width: 108px;
  }
}

.houses {
  position: relative;
  width: 1709px;
  height: 1324px;
  display: flex;
  justify-content: center;
  overflow: hidden;
  border-bottom-left-radius: 384px;
  border-bottom-right-radius: 418px;
  border-top-left-radius: 515px;
  border-top-right-radius: 368px;
}
.houses:hover .houses__lighter {
  opacity: 1;
}
@media (max-width: 1742px) {
  .houses {
    width: 1280px;
    height: 996px;
    border-bottom-left-radius: 295px;
    border-bottom-right-radius: 319px;
    border-top-left-radius: 397px;
    border-top-right-radius: 279px;
  }
}
@media (max-width: 1279px) {
