    width: 375px;
    height: 1072px;
  }
}
.houses__image {
  position: absolute;
  z-index: 0;
  height: 100%;
  width: 100%;
  background-image: url("./../images/pallete@1x.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .houses__image {
    background-image: url("./../images/pallete@2x.png");
  }
}
@media (max-width: 1742px) {
  .houses__image {
    background-size: 1280px 996px;
  }
}
@media (max-width: 1279px) {
  .houses__image {
    background-size: contain;
    background-image: url("./../images/pallete-tablet@1x.png");
  }
}
@media (max-width: 1279px) and (min-resolution: 144dpi), (max-width: 1279px) and (min-resolution: 1.5dppx) {
  .houses__image {
    background-image: url("./../images/pallete-tablet@2x.png");
  }
}
@media (max-width: 1017px) {
  .houses__image {
    background-image: url("./../images/pallete-mob.svg");
  }
}
.houses__lighter {
  z-index: 2;
  position: absolute;
  background-image: url("./../images/pallete-colored@1x.png");
  mask: radial-gradient(circle, rgba(0, 0, 0, 0.9) 180px, transparent 200px);
  -webkit-mask: radial-gradient(circle, rgba(0, 0, 0, 0.9) 180px, transparent 200px);
  height: 440px;
  width: 440px;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 300ms;
  background-size: 1709px 1324px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .houses__lighter {
    background-image: url("./../images/pallete-colored@2x.png");
  }
}
.houses__lighter::before {
  content: "";
  position: absolute;
  display: inline-block;
  z-index: 2;
  width: 106px;
  height: 116px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("./../images/gaslighter.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
@media (max-width: 1742px) {
  .houses__lighter {
    background-size: 1280px 996px;
  }
}
@media (max-width: 1279px) {
  .houses__lighter {
    display: none;
  }
}

