.footer {
  height: 225px;
  margin-top: 100px;
  background-color: #e4efff;

  @media (max-width: 1279px) {
    margin-top: 50px;
    height: 120px;
  }

  @media (max-width: 767px) {
    height: 47px;
    margin-top: 55px;
  }

  &__container {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1617px;
    height: 100%;

    @media (max-width: 767px) {
      padding: 0 34px; 
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

.popup__overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background: rgba(0, 0, 0, 0.35);
}

.snowContainer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 15;
  pointer-events: none;
}

#snow {
  width: 100%;
  height: 100%;
  background-image: url("./../images/snow/snow_1.png"),
    url("./../images/snow/snow_2.png"), url("./../images/snow/snow_3.png");
  -webkit-animation: snow 20s linear infinite;
  -moz-animation: snow 20s linear infinite;
  -ms-animation: snow 20s linear infinite;
  animation: snow 20s linear infinite;
}

@keyframes snow {
  0% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }
  100% {
    background-position: 500px 1000px, 400px 400px, 300px 300px;
  }
}

@-moz-keyframes snow {
  0% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }
  100% {
    background-position: 500px 1000px, 400px 400px, 300px 300px;
  }
}

@-webkit-keyframes snow {
  0% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }
  100% {
    background-position: 500px 1000px, 400px 400px, 300px 300px;
  }
}

@-ms-keyframes snow {
  0% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }
  100% {
    background-position: 500px 1000px, 400px 400px, 300px 300px;
  }
}

.popup {
  &__close {
    position: absolute;
    top: 13px;
    right: 11px;
    cursor: pointer;

    @media (max-width: 767px) {
      top: 5px;
      right: 5px;
    }
  }

  &__wrapper {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 11;
      width: 647px;
      height: 171px;
      background: url("./../images/popup-footer.png");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: -49px 75px;

      @media (max-width: 767px) {
        width: 335px;
        height: 95px;
        background-position: -45px 40px;
      }
    }
  }

  &__content {
    position: relative;
    z-index: 10;
    width: 640px;
    height: 603px;
    padding: 34px;
    background: #e4efff;

    @media (max-width: 767px) {
      width: 335px;
      height: 500px;
      padding: 25px;
    }
  }

  &__title {
    margin-bottom: 16px;
    font-size: 18px;
    line-height: 21px;
    font-weight: normal;
    text-align: center;
    font-family: "Lato", sans-serif;
  }

  &__text {
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    font-family: "Lato", sans-serif;
  }
}

.slider {
  padding-bottom: 5px;
  margin-bottom: 24px;
  border-bottom: 1px solid #d2d4d7;
  color: transparent;

  @media (max-width: 767px) {
    padding-bottom: 0;
  }

  .slick-prev,
  .slick-next {
    z-index: 10;
    top: auto;
    bottom: -30px;
    width: 75px;
    height: 60px;
    cursor: pointer;

    @media (max-width: 767px) {
      bottom: -15px;
      width: 50px;
      height: 40px;
    }
  }

  .slick-prev:hover,
  .slick-prev:focus {
    color: transparent;
    outline: none;
    background-image: url("./../images/arrow-left.png");
    background-size: cover;
    background-repeat: no-repeat;
  }
  .slick-next:hover,
  .slick-next:focus {
    color: transparent;
    outline: none;
    background-image: url("./../images/arrow-right.png");
    background-size: cover;
    background-repeat: no-repeat;
  }

  .slick-prev {
    left: 0;
    background-image: url("./../images/arrow-left.png");
    background-size: cover;
    background-repeat: no-repeat;
  }

  .slick-next {
    right: 0;
    background-image: url("./../images/arrow-right.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .slick-prev:before,
  .slick-next:before {
    display: none;
  }
}
.slick-slider {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.slide {
  outline: none;
  width: 100%;
  &__image {
    width: 100%;
    margin-bottom: 24px;

    @media (max-width: 767px) {
      margin-bottom: 12px;
    }
  }

  &__comment {
    text-align: center;
    font-size: 10px;
    line-height: 11px;
    font-family: "Lato", sans-serif;
    color: black;
  }
}
